	module.exports = {"data":{"markdownRemark":{"frontmatter":{"title":"VNUTRI GERBARIYA","type":"lyrics"},"html":"<h1>VNUTRI GERBARIJA</h1>\n<section>\n<p>segodnja osobenno vetreno<br>\nosobenno v chashhe<br>\nosobenno v chashe<br>\nlesa osobenno vetreno</p>\n<p>lezhim na zhuhloj listve<br>\nv obnimku nagie<br>\nv oblipku nogi i<br>\nrjobra na mokroj listve</p>\n<p>vokrug zakruglilis' derev'ja<br>\nshumjashhim zaborom<br>\nshumjat im za borom<br>\nvselennye budto derev'ja</p>\n<p>v verhu razryhlilos' solnce<br>\ntechjot kosymi luchami<br>\ntechjot kosulja‐m[i je]chami<br>\nv verhu rasshirjalos' solnce</p>\n<p>a u tebja volosy pal'cy<br>\nja sobiraju iz nih ladoni<br>\ni moljus' na nih kak ital'jancy<br>\nsikstinskoj madonne<br>\na u menja golova tajnik<br>\noshibok vsego chelovechestva<br>\nty moj bezumnyj tik<br>\ni jeto nikak ne lechitsja<br>\nne vyvetritsja ne ujdjot<br>\na esli smotret' s neba<br>\nnashe lozhe ogromnyj rot<br>\ngde my kusochkami hleba<br>\nnasushhnogo dnes' razleglis'<br>\nlezhim na kraju u centra</p>\n<p>Stjanulas' tuchami vys'<br>\ni sprjatala nas ot vetra.</p>\n</section>"},"site":{"siteMetadata":{"languages":{"defaultLangKey":"en","langs":["en","ru"]}}}},"pathContext":{"slug":"/en/lyrics/vnutriGerbariya/"}}

